:host {
  --pfe-band--Padding--vertical: calc( var(--pfe-theme--container-spacer, 16px) * 4);
  --pfe-band--Padding--horizontal: calc( var(--pfe-theme--container-spacer, 16px) * 1);
  --pfe-band--Padding: var(--pfe-band--Padding--vertical) var(--pfe-band--Padding--horizontal);
  --pfe-band--BackgroundColor: var(--pfe-theme--color--surface--base, #dfdfdf);
  --pfe-band--BackgroundPosition: center center;
  --pfe-band--Border: var(--pfe-theme--surface--border-width, 1px) var(--pfe-theme--surface--border-style, solid) transparent;
  --pfe-band--layout: 1fr;
  --pfe-band__header--layout: 1fr;
  --pfe-band__body--layout: 1fr;
  --pfe-band__footer--layout: 1fr;
  --pfe-band__aside--layout: 1fr;
  --pfe-band--gutter--vertical: var(--pfe-theme--container-spacer, 16px);
  --pfe-band--gutter--horizontal: calc(var(--pfe-theme--container-spacer, 16px) * 3);
  --pfe-band--Width: auto;
  --pfe-band--Width__aside--sm: 240px;
  --pfe-band--Width__aside--lg: 300px;
  --pfe-broadcasted--text: var(--pfe-theme--color--text, #333);
  --pfe-broadcasted--link: var(--pfe-theme--color--link, #06c);
  --pfe-broadcasted--link--hover: var(--pfe-theme--color--link--hover, #003366);
  --pfe-broadcasted--link--focus: var(--pfe-theme--color--link--focus, #003366);
  --pfe-broadcasted--link--visited: var(--pfe-theme--color--link--visited, rebeccapurple);
  --pfe-broadcasted--link-decoration: none;
  --pfe-broadcasted--link-decoration--hover: underline;
  --pfe-broadcasted--link-decoration--focus: underline;
  --pfe-broadcasted--link-decoration--visited: none;
  --theme: light;
  display: block;
  position: relative;
  padding: calc(var(--pfe-band--Padding--vertical) / 2) var(--pfe-band--Padding--horizontal);
  border: var(--pfe-band--Border);
  background-color: var(--pfe-band--BackgroundColor);
  background-position: var(--pfe-band--BackgroundPosition);
  color: var(--pfe-broadcasted--text, #333);
}

@media screen and (min-width: 768px) {
  :host {
    --pfe-band--Width: calc( 768px - calc(var(--pfe-band--Padding--horizontal) * 4) );
  }
}

@media screen and (min-width: 992px) {
  :host {
    --pfe-band--Width: calc( 992px - calc(var(--pfe-band--Padding--horizontal) * 4) );
  }
}

@media screen and (min-width: 1200px) {
  :host {
    --pfe-band--Width: calc( 1200px - calc(var(--pfe-band--Padding--horizontal) * 4) );
  }
}

@media (min-width: 576px) {
  :host {
    padding: var(--pfe-band--Padding);
  }
}

@media print {
  :host {
    background-color: white !important;
    background-image: none !important;
    box-shadow: none !important;
  }
}

@media print {
  :host {
    border-radius: 3px;
    border: 1px solid #d2d2d2;
    padding: calc(var(--pfe-band--Padding--vertical) / 2) var(--pfe-band--Padding--horizontal);
  }
}

@media screen and (-ms-high-contrast: active), screen and (-ms-high-contrast: none) {
  :host {
    /* IE10+ */
    background-color: var(--pfe-theme--color--surface--lightest, #fff) !important;
    color: var(--pfe-theme--color--text, #333) !important;
    background-image: none !important;
    padding: calc(var(--pfe-theme--container-spacer, 16px));
  }
}

:host *, :host *::before, :host *::after {
  box-sizing: border-box;
}

:host([on="dark"]) {
  --pfe-broadcasted--text: var(--pfe-theme--color--text--on-dark, #fff);
  --pfe-broadcasted--link: var(--pfe-theme--color--link--on-dark, #99ccff);
  --pfe-broadcasted--link--hover: var(--pfe-theme--color--link--hover--on-dark, #cce6ff);
  --pfe-broadcasted--link--focus: var(--pfe-theme--color--link--focus--on-dark, #cce6ff);
  --pfe-broadcasted--link--visited: var(--pfe-theme--color--link--visited--on-dark, #b38cd9);
  --pfe-broadcasted--link-decoration: none;
  --pfe-broadcasted--link-decoration--hover: underline;
  --pfe-broadcasted--link-decoration--focus: underline;
  --pfe-broadcasted--link-decoration--visited: none;
}

@media screen and (-ms-high-contrast: active), screen and (-ms-high-contrast: none) {
  /* IE10+ */
  :host {
    color: var(--pfe-theme--color--text, #333) !important;
  }
}

:host([on="saturated"]) {
  --pfe-broadcasted--text: var(--pfe-theme--color--text--on-saturated, #fff);
  --pfe-broadcasted--link: var(--pfe-theme--color--link--on-saturated, #fff);
  --pfe-broadcasted--link--hover: var(--pfe-theme--color--link--hover--on-saturated, white);
  --pfe-broadcasted--link--focus: var(--pfe-theme--color--link--focus--on-saturated, white);
  --pfe-broadcasted--link--visited: var(--pfe-theme--color--link--visited--on-saturated, #b38cd9);
  --pfe-broadcasted--link-decoration: underline;
  --pfe-broadcasted--link-decoration--hover: underline;
  --pfe-broadcasted--link-decoration--focus: underline;
  --pfe-broadcasted--link-decoration--visited: underline;
}

@media screen and (-ms-high-contrast: active), screen and (-ms-high-contrast: none) {
  /* IE10+ */
  :host {
    color: var(--pfe-theme--color--text, #333) !important;
  }
}

:host([on="light"]) {
  --pfe-broadcasted--text: var(--pfe-theme--color--text, #333);
  --pfe-broadcasted--link: var(--pfe-theme--color--link, #06c);
  --pfe-broadcasted--link--hover: var(--pfe-theme--color--link--hover, #003366);
  --pfe-broadcasted--link--focus: var(--pfe-theme--color--link--focus, #003366);
  --pfe-broadcasted--link--visited: var(--pfe-theme--color--link--visited, rebeccapurple);
