@supports (display: grid) {
  .pfe-band__container {
    display: grid;
    grid-row-gap: var(--pfe-band--gutter--vertical);
    grid-column-gap: var(--pfe-band--gutter--horizontal);
    margin-bottom: 0;
    grid-template-columns: var(--pfe-band--layout);
    grid-template-rows: max-content;
  }
}

.pfe-band__header {
  margin-bottom: var(--pfe-band--gutter--vertical);
}

@supports (display: grid) {
  .pfe-band__header {
    display: grid;
    grid-row-gap: var(--pfe-band--gutter--vertical);
    grid-column-gap: var(--pfe-band--gutter--horizontal);
    margin-bottom: 0;
    grid-area: header;
    grid-template-columns: var(--pfe-band__header--layout);
  }
}

.pfe-band__body {
  margin-bottom: var(--pfe-band--gutter--vertical);
}

@supports (display: grid) {
  .pfe-band__body {
    display: grid;
    grid-row-gap: var(--pfe-band--gutter--vertical);
    grid-column-gap: var(--pfe-band--gutter--horizontal);
    margin-bottom: 0;
    grid-area: body;
    grid-template-columns: var(--pfe-band__body--layout);
  }
}

.pfe-band__aside {
  margin-bottom: var(--pfe-band--gutter--vertical);
}

@supports (display: grid) {
  .pfe-band__aside {
    display: grid;
    grid-row-gap: var(--pfe-band--gutter--vertical);
    grid-column-gap: var(--pfe-band--gutter--horizontal);
    margin-bottom: 0;
    grid-area: aside;
    grid-template-columns: var(--pfe-band__aside--layout);
  }
}

.pfe-band__footer {
  margin-bottom: var(--pfe-band--gutter--vertical);
}

@supports (display: grid) {
  .pfe-band__footer {
    display: grid;
    grid-row-gap: var(--pfe-band--gutter--vertical);
    grid-column-gap: var(--pfe-band--gutter--horizontal);
    margin-bottom: 0;
    grid-area: footer;
    grid-template-columns: var(--pfe-band__footer--layout);
  }
}

.pfe-band__aside {
  align-self: flex-start;
}

@media screen and (-ms-high-contrast: active), screen and (-ms-high-contrast: none) {
  .pfe-band {
    /* IE10+ */
  }
  .pfe-band__body {
    width: 60%;
    float: left;
  }
  .pfe-band__aside {
    float: left;
    width: 35%;
    margin: 0 2.5%;
  }
  .pfe-band__footer {
    clear: both;
  }
  .pfe-band__container::after {
    content: " ";
    display: block;
    clear: both;
  }
}
